import React, { PureComponent } from "react";
import classNames from "classnames";
import style from "./style.scss";
import Progress from "../Progress";
import { Button } from "mdbreact";
import { withRouter } from "react-router-dom";
import { getPrevNextIdInList } from "util/Common";
import { finishLectureRequest } from "store/Course/action";
import { connect } from "react-redux";
import _ from "lodash";

class Index extends PureComponent {
  render() {
    const {
      course: {
        id,
        totalLecture,
        lecture: { list: allLectures, finishList: lectureFinishList }
      },
      match: {
        params: { lectureId }
      },
      history,
      finishLectureRequest
    } = this.props;
    const { prevId, nextId, isFirst, isLast } = getPrevNextIdInList({
      id: lectureId,
      list: allLectures
    });
    const progress = (lectureFinishList.length / totalLecture) * 100;
    const checkLecFinished = _.includes(lectureFinishList, lectureId);
    return (
      <div
        className={classNames("bg-white p-3 p-md-4 border-bottom", style.top)}
      >
        <h6 className="h6-responsive font-weight-bold">
          {lectureFinishList.length} of {totalLecture} lectures complete
        </h6>
        <Progress progress={progress} />
        <div className="container mt-3">
          <div className="row">
            <div className="col-6 col-md-5 pl-0">
              <Button
                disabled={isFirst}
                color="primary"
                className="w-100 ml-0 px-3"
                onClick={() => {
                  history.push(`/course/${id}/lecture/${prevId}`);
                }}
              >
                previous
              </Button>
            </div>
            <div className="col-6 col-md-5 offset-md-2 pr-1">
              <Button
                disabled={isLast}
                color="primary"
                className="w-100"
                onClick={() => {
                  history.push(`/course/${id}/lecture/${nextId}`);
                  !checkLecFinished &&
                    finishLectureRequest({
                      id,
                      lectureId
                    });
                }}
              >
                next
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const connectIndex = connect(
  undefined,
  { finishLectureRequest }
)(Index);

export default withRouter(connectIndex);
